      "27일(토) 방송되는 1부 '캣 센스' 편에서는 고양이의 본능적인 습성과 감각에서 비롯된 반전 매력에 주목한다. 최근 냥스타그램이라 불리는 고양이의 사진과 동영상을 주로 올리는 SNS계정의 폭발적인 인기를 얻고 있다. 인터넷세상에서 나마 집사가 되고 싶은 '랜선집사'들의 뜨거운 관심으로 수백만의 팔로워와 구독자를 보유한 수많은 스타 고양이들이탄생한 것. 고양이의 집사를 자처하며 열광하는 사람들의 힘입어 고양이는 인간의 영역 더 깊숙한 곳으로 스며들고 있다. 그중에는 국민 가수, 국민 스포츠 스타의 인기 부럽지 않은 냥스타들도 있다. 고양이들의 무엇이 우릴 매료 시키고 있는 것일까? 인간과 고양이가 만난 건 1만 년 전. 그 이후 지금까지 줄곧 인간과 교류해오면서도 고양이는 야생의 모습과 본능, 능력을 잃지 않고 유지해오고 있다. 시간이 지나도 길들여지지 않는 도도함, 그리고 자신들의 취향을 고수하는 그 고집스러움은 때론 고양이들을 이해 못 할 존재로 만들기도 한다. 이들의 매력을 1부 '캣 센스'에서 알아본다.",
      "4월 3일(토)에방송되는 2부 '고양이, 인간의 곁에 오다' 편에서는 인간과 고양이가 아름답게 공존하는 방법에 대해 이야기한다. 고양이에게는 길고양이와 집고양이라는 두 가지 이름이 있다. 불리는 이름에 따라 고양이의 운명은 극명하게 갈린다. 길고양이는 거리를 더럽히는 천덕꾸러기이고, 집고양이는 인생을 나누는 반려묘로 여겨진다. 고양이만큼 인간과의 관계에서 스펙터클한 굴곡의 역사를 가진 동물은 아마 없었을 것. 고대이집트에서는 신으로도 추앙되었던 고양이는 중세시대에는 마녀와 동급으로 여겨, 살처분 되기도 했다. 그리고 중세 이후 지금까지 고양이를 보는 편견어린 시선들은 여전히 남아있다. '목숨이 9개인 영물' '기분 나쁘다''무섭다' 등. 그리고 그저 싫다는 이유만으로 외면 받고, 심지어는 짓밟히기까지 하는 고양이들. 유례없는 고양이 열풍 속에 버려지는 고양이는 더 많아진 아이러니한 상황이다. 인간의 변덕스러운 마음에 따라 천국과 지옥을 오갔고, 여전히 두 개의 다른 이름으로 불리며, 생존까지 위협받고 있다. 같은 공간에 살아가는 인간과 고양이의 공존 방법, 그 해답을 찾아본다.",
